import Vue from 'vue'
import VueRouter from 'vue-router'
const Login = () => import(/* webpackChunkName: "Login" */ '../views/Login.vue')
const Admin = () => import(/* webpackChunkName: "Admin" */ '../views/Admin.vue')

// 页面路由组件
const Index = () => import(/* webpackChunkName: "Index" */ '../components/admin/Index.vue')
const AddArticle = () => import(/* webpackChunkName: "AddArt" */ '../components/article/AddArticle.vue')
const ArticleList = () => import(/* webpackChunkName: "ArtList" */ '../components/article/ArticleList.vue')
const CategoryList = () => import(/* webpackChunkName: "CateList" */ '../components/category/CategoryList.vue')
const UserList = () => import(/* webpackChunkName: "UserList" */ '../components/user/UserList.vue')
const Profile = () => import(/* webpackChunkName: "UserList" */ '../components/user/Profile.vue')
const CommentList = () => import(/* webpackChunkName: "UserList" */ '../components/comment/CommentList.vue')

// 路由重复点击捕获错误
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location, onResolve, onReject) {
    if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
    return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)

const routes = [
    {
        path: '/login',
        name: 'login',
        meta: {
            title: '请登录'
        },
        component: Login
    },
    {
        path: '/',
        name: 'admin',
        meta: {
            title: 'Blog后台管理页面'
        },
        component: Admin,
        children: [
            {
                path: 'index',
                component: Index,
                meta: {
                    title: 'Blog后台管理页面'
                }
            },
            {
                path: 'addarticle',
                component: AddArticle,
                meta: {
                    title: '新增文章'
                }
            },
            {
                path: 'addarticle/:id',
                component: AddArticle,
                meta: {
                    title: '编辑文章'
                },
                props: true
            },
            {
                path: 'articlelist',
                component: ArticleList,
                meta: {
                    title: '文章列表'
                }
            },
            {
                path: 'categorylist',
                component: CategoryList,
                meta: {
                    title: '分类列表'
                }
            },
            {
                path: 'userlist',
                component: UserList,
                meta: {
                    title: '用户列表'
                }
            },
            {
                path: 'profile',
                component: Profile,
                meta: {
                    title: '个人设置'
                }
            },
            {
                path: 'commentlist',
                component: CommentList,
                meta: {
                    title: '评论管理'
                }
            }
        ]
    }
]

const router = new VueRouter({
    mode: 'hash',
    routes
})

router.beforeEach((to, from, next) => {

    if (to.meta.title) {
        document.title = to.meta.title ? to.meta.title : '加载中'
    }
    next()

    const userToken = window.sessionStorage.getItem('token')
    if (to.path === '/login') return next()
    if (!userToken) {
        next('/login')
    } else {
        next()
    }
})

export default router
